:root {
  --control-border-radius: 10px;
}

:root,
.dark-theme {
  --colors-background-destructive: #911;
  --colors-text-destructive: white;
  /* --colors-background-selected: #666; */
  --colors-background: #333;

  /* --colors-item-container-background: #111; */
  /* --colors-item-selected: #669; */
  /* --colors-item-special: #336; */
  /* --colors-text-selected: white; */
  --colors-background-workspace: #333;
  --colors-background-panel: #35363a;
  --colors-background-notification: lightgreen;

  --colors-control-default: #261f2b;
  --colors-control-edge: #eeeeee4d;
  --colors-control-highlight: #494;
  --colors-control-active: #484;
  --colors-control-active-dimmed: #484c;
  --colors-control-focus: #4a4;

  --colors-text-default: #eee;
  --colors-text-dimmed: #aaa;
  --colors-text-active: #5b5;
}

@media (prefers-color-scheme: light) {
  :root {
    --colors-background-destructive: #911;
    --colors-text-destructive: #eee;
    --colors-background: #fff;

    /* --colors-background-selected: #bbe; */
    /* --colors-background: #eee;
    --colors-item-container-background: #ccc;
    --colors-item-selected: #bbe;
    --colors-item-special: #366;
    --colors-text-selected: black;
    --colors-text: #333; */

    --colors-background-workspace: #a9a9a9;
    --colors-background-panel: #e7eaed;
    --colors-background-notification: red;

    --colors-control-default: #fff;
    --colors-control-edge: #33333334;
    --colors-control-highlight: #cce;
    --colors-control-active-dimmed: #bbec;
    --colors-control-active: #bbe;
    --colors-control-focus: #66e;

    --colors-text-default: #333;
    --colors-text-dimmed: #3338;
    --colors-text-active: #669;
  }
}

.light-theme {
  --colors-background-destructive: #911;
  --colors-text-destructive: #eee;
  --colors-background: #fff;

  /* --colors-background-selected: #bbe; */
  /* --colors-background: #eee;
    --colors-item-container-background: #ccc;
    --colors-item-selected: #bbe;
    --colors-item-special: #366;
    --colors-text-selected: black;
    --colors-text: #333; */

  --colors-background-workspace: #a9a9a9;
  --colors-background-panel: #e7eaed;
  --colors-background-notification: red;

  --colors-control-default: #fff;
  --colors-control-edge: #33333334;
  --colors-control-highlight: #cce;
  --colors-control-active-dimmed: #bbec;
  --colors-control-active: #bbe;
  --colors-control-focus: #66e;

  --colors-text-default: #333;
  --colors-text-dimmed: #3338;
  --colors-text-active: #669;
}
